* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: "Lato", sans-serif;
}

html {
    /* background: rgb(2, 0, 36);
        background: radial-gradient(circle, rgba(2, 0, 36, 1) 0%, rgba(9, 9, 121, 1) 65%, rgba(0, 212, 255, 1) 100%); */
    
    background: rgb(9, 9, 121);
        background: radial-gradient(circle, rgba(9, 9, 121, 1) 0%, rgba(15, 15, 153, 1) 65%, rgba(0, 212, 255, 1) 100%);

    box-sizing: border-box;
    font-family: "Lato", sans-serif;
    font-size: 14px;
    font-weight: 400;
}

*,
*:before,
*:after {
    box-sizing: inherit;
}

.subtle {
    color: rgb(84, 80, 80);
}

.main-card--container {
    display: grid;
    grid-template-columns: repeat(3, minmax(300px, 500px));
    grid-gap: 25px;
    justify-content: center;
}

.card-container {
    margin: 30px auto 0;
    position: relative;
    display: grid;
    grid-template-columns: 1fr;
    justify-content: space-around;
    align-items: flex-start;
}

.card-container img {
    max-width: 100%;
    height: 250px;
}

.card {    
    background-color: #ffffff;
    padding: 10px;
    position: relative;
    box-shadow: 10px 10px 10px rgb(16, 16, 16);
    z-index: 1;
}

.card-body {
    justify-content: center;
    display: inline-block;
}

.card-author {
    display: block;
    font-size: 13px;
    letter-spacing: 0.5px;
    margin: 15px 0 0;
    text-transform: capitalize;
}

.card-title {
    font-family: "Cormorant Garamond", serif;
    font-size: 60px;
    font-weight: 300;
    line-height: 60px;
    margin: 10px 0;
    text-transform: capitalize;
}

.card-description {
    /* display: inline-block; */
    font-weight: 300;
    line-height: 22px;
    margin: 10px 0;
}

