.card-tag {
    float: right;
    margin: 5px 0 0;
    border: 1px solid #aaa;
    padding: 5px 10px;
    cursor: pointer;
}

.card-tag:hover {
    background: #aaa;
    color: black;
}

.card-media {
    max-width: 100%;
    height: auto;
}

.card-shadow {
    background-color: #fff;
    box-shadow: 0 2px 25px 2px rgba(0, 0, 0, 1), 0 2px 50px 2px rgba(0, 0, 0, 1),
        0 0 100px 3px rgba(0, 0, 0, 0.25);
    height: 1px;
    margin: -1px auto 0;
    width: 80%;
    z-index: -1;
}

@media (max-width: 998px) {
    .main-card--container {
        display: grid;
        grid-template-columns: repeat(1, minmax(200px, 500px));
        grid-gap: 50px;
        justify-content: space-around;
    }
}

/* ---------- Button styles ------------------ */

.navbar {
    padding-top: 30px;
    text-align: center;
}

.navbar h1 {
    padding-bottom: 20px;
    text-transform: capitalize;
}

/** BUTTON GROUP STYLES **/
.btn-group {
    border-radius: 1rem;
    text-transform: capitalize;
}

.btn-group__item {
    border: none;
    /* min-width: 6rem; */
    padding: 1rem 1.5rem;
    /* background-color: rgb(255, 255, 255); */
    background-color: #002244;
    color: white;
    cursor: pointer;
    margin: 0;
    font-size: 17px;
    box-shadow: inset 0px 0px 0px -15px #00d4ff;
    transition: all 300ms ease-out;
    text-transform: capitalize;
}

.btn-group__item:last-child {
    border-top-right-radius: 1rem;
    border-bottom-right-radius: 1rem;
}

.btn-group__item:first-child {
    border-top-left-radius: 1rem;
    border-bottom-left-radius: 1rem;
}

.btn-group__item:hover,
.btn-group__item:focus {
    color: black;
    background-color: #caf3ff;
    box-shadow: inset 0px -20px 0px -15px #00CCFF;
    /* box-shadow: inset 0px -20px 0px -15px #002244; */
}

.btn-group__item:focus {
    outline: none;
}

.btn-group__item:after {
    content: "✔️";
    margin-left: 0.5rem;
    display: inline-block;
    color: #00CCFF;
    position: absolute;
    transform: translatey(10px);
    opacity: 0;
    transition: all 200ms ease-out;
}

.btn-group__item--active:after {
    opacity: 1;
    transform: translatey(-2px);
}